<template>
  <div class="page">
    <AnimateHeader
      bg-color="#F5F5F5 "
      menu-bg-color="#F0F0F0"
      color="#3A3A3A"
      fill="#3A3A3A"
      stroke="#3A3A3A"
      title="选择提现方式"
      :on-back-button-click="back"
      :expanded="false"
      :animate="false"
    >
    </AnimateHeader>
    <div class="page-content withdraw-type">
      <div class="recommend">
        <div class="title">推荐方式</div>
        <div class="type" @click="go('phone')">
          <img src="//assets.qkcdn.com/images/de67881df82d1a56205b13a10096d9ea.png" alt="">
          <div class="desc-content">
            <div class="type-name">手机充值</div>
            <div class="desc">支持全号码段 ‧ 到账最快</div>
            <span class="song-chou-ma"></span>
          </div>
        </div>

        <div class="type" @click="go('wechat')">
          <img src="//assets.qkcdn.com/images/381738ff37b4bda755ef2234e8ed3f6a.png" alt="">
          <div class="desc-content">
            <div class="type-name">微信提现</div>
            <div class="desc">支持500元以上大额提现</div>
            <span class="song-chou-ma"></span>
          </div>
        </div>
      </div>

      <div class="recommend">
        <div class="title">其他方式</div>
        <div class="type" @click="go('alipay')">
          <img src="//assets.qkcdn.com/images/0d979e6aad7ebd524fc89941f5855983.png" alt="">
          <div class="desc-content">
            <div class="type-name" style="line-height: 38px;">支付宝提现</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  @import "../../../scss/mixins";

  .withdraw-type {
    background: #FFF;
    padding-top: 50px;
    padding-left: 21px;

    .recommend {
      margin-bottom: 41px;

      .title {
        height: 28px;
        line-height: 28px;
        font-size: 20px;
        color: #DDDDDD;
        margin-top: 24px;
        margin-bottom: 25px;

        @include font-regular();
      }

      .type {
        height: 38px;
        margin-bottom: 42px;
        padding-left: 55px;

        img {
          width: 38px;
          height: 38px;
          position: absolute;
          left: 21px;
        }

        .desc-content {
          position: relative;
          display: inline-block;

          .type-name {
            height: 22px;
            font-size: 16px;
            color: #484746;
            display: block;
            margin-bottom: 6px;

            @include font-regular();
          }

          .desc {
            display: block;
            height: 18px;
            font-size: 13px;
            color: #B5B5B5;
          }

          .alipay {
            font-size: 16px;
            color: #484746;
            height: 46px;
            line-height: 46px;
            display: block;

            @include font-regular();
          }

          .song-chou-ma {
            position: absolute;
            top: -14px;
            left: 68px;
            display: inline-block;
            width: 35px;
            height: 18px;
            background: url('//qianka.b0.upaiyun.com/images/edecbfae42aba79edfaf617090a7fc1a.png') no-repeat;
            background-size: 100% 100%;
          }
        }
      }
    }
  }

</style>
<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  export default {
    components: {
      AnimateHeader
    },

    methods: {
      back () {
        $router.back({
          path: '/user'
        })
      },

      go (type) {
        let path = type === 'phone' ? '/withdraw/phone' : (type === 'wechat' ? '/withdraw/weixin' : '/withdraw/alipay')
        $router.push(path)
      }
    }
  }
</script>



